import React, { useState } from 'react'
import styled  from 'styled-components';
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Container from '@material-ui/core/Container'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import TextField from '@material-ui/core/TextField'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import { useHistory } from 'react-router';

const formSpace = `
margin-top: 30px;
margin-bottom: 20px;
display: block;
`;

const StyledTextField = styled(TextField)`

${formSpace}
`;

const StyledFormControl = styled(FormControl)`

${formSpace}
`;

const StyledFormLabel = styled(FormLabel)`

${formSpace}
`;



export default function Create() {
  const history = useHistory()
  const [title, setTitle] = useState('')
  const [details, setDetails] = useState('')
  const [titleError, setTitleError] = useState(false)
  const [detailsError, setDetailsError] = useState(false)
  const [category, setCategory] = useState('money')

  const handleSubmit = (e) => {
    e.preventDefault()
    setTitleError(false)
    setDetailsError(false)

    if (title == '') {
      setTitleError(true)
    }
    if (details == '') {
      setDetailsError(true)
    }
    if (title && details) {
      fetch('https://fake-server808.herokuapp.com/notes', {
        method: 'POST',
        headers: {"Content-type": "application/json"},
        body: JSON.stringify({ title, details, category })
      }).then(() => history.push('/'))
    } 
  }

  return (
    <Container size="sm">
      <Typography
        variant="h6" 
        color="textSecondary"
        component="h2"
        gutterBottom
      >
        Create a New Note (Please be kind)
      </Typography>
      
      <form noValidate autoComplete="off" onSubmit={handleSubmit}>
      <StyledTextField
          onChange={(e) => setTitle(e.target.value)}
          label="Note Title" 
          variant="outlined" 
          color="primary" 
          fullWidth
          required
          error={titleError}
        />
        <StyledTextField
          onChange={(e) => setDetails(e.target.value)}
          label="Details"
          variant="outlined"
          color="primary"
          multiline
          rows={4}
          fullWidth
          required
          error={detailsError}
        />

        <StyledFormControl>
        <StyledFormLabel color="primary">Note Category</StyledFormLabel>
          <RadioGroup value={category} onChange={(e) => setCategory(e.target.value)}>
            <FormControlLabel control={<Radio color="primary" />} value="money" label="Money" />
            <FormControlLabel control={<Radio color="primary" />} value="todos" label="Todos" />
            <FormControlLabel control={<Radio color="primary" />} value="reminders" label="Reminders" />
            <FormControlLabel control={<Radio color="primary" />} value="work" label="Work" />
          </RadioGroup>
        </StyledFormControl>


        <Button
          type="submit" 
          color="primary" 
          variant="contained"
          endIcon={<KeyboardArrowRightIcon />}>
          Submit
        </Button>
      </form>

      
    </Container>
  )
}
